.poll-form {
  text-align: center;

  form {
    display: inline-block;
    text-align: left;
  }
}

.poll-results {
  canvas {
    width: 100%;
    height: 220px;
    display: block;
    float: right;
  }
  ul {
    list-style-type: none;
    padding: 0 10px 10px;
    max-width: 300px;
  }
  li {
    border-left: 8px solid #fff;
    padding-left: 6px;
    margin: 5px 0;
  }
  .chosen {
    font-weight: bold;
  }

  @media (max-width: 991px) {
    canvas {
      margin: 0 auto;
      float: none;
    }
    ul {
      margin: 0 auto;
    }
  }
}